import React from "react";
import cx from "classnames";
import Slideshow from "../../../components/Slideshow";
import AddDialog from "../../../components/AddDialog";
import { getAppLibraryDetail, installApp, getLibraryToken } from "/lib/api";
import Load from "/src/components/Load";
import { pick, isEqual, map } from "lodash";
import Linkify from "react-linkify";
import { Link } from "react-router-dom";
import VideoCon from "../../../components/VideoCon";
import SvgIcon from "/src/components/SvgIcon";
import { Modal } from "antd";
class AppDetails extends React.Component {
  constructor(props) {
    super(props);
    this.saveRef = (ref) => {
      this.refDom = ref;
    };
  }
  state = {
    showVideo: false,
    showDialog: false,
    data: [],
  };

  componentDidMount() {
    window.scrollTo(0, 0);
    this.fetch();
  }

  componentDidUpdate(prevProps, prevState) {
    const keys = [].concat(["libraryId"]);
    if (!isEqual(pick(this.props, keys), pick(prevProps, keys))) {
      this.fetch();
    }
  }

  showVideoDialog = () => {
    this.setState({ showVideo: true });
  };

  IsPC = () => {
    let userAgentInfo = navigator.userAgent;
    let Agents = [
      "Android",
      "iPhone",
      "SymbianOS",
      "Windows Phone",
      "iPad",
      "iPod",
    ];
    let flag = true;
    for (let v = 0; v < Agents.length; v++) {
      if (userAgentInfo.indexOf(Agents[v]) > 0) {
        flag = false;
        break;
      }
    }
    return flag;
  };

  fetch = () => {
    getAppLibraryDetail({
      libraryId: this.props.libraryId,
    }).then((res) => {
      if (res.length <= 0) {
        let url = !this.props.accountId ? "/library" : "/app/lib";
        window.location.assign(url);
      } else {
        this.setState({
          ...this.state,
          data: res.data.data,
        });
      }
    });
  };

  installAppFn = (projectId, libraryId) => {
    const { isMobile } = this.props;
    const cancelFn = (explainText, hint, data) => {
      this.setState({ showDialog: false });
      if (window.VersionDialog) {
        window.VersionDialog(data);
      } else if (window.upgradeVersionDialog) {
        window.upgradeVersionDialog({
          projectId,
          explainText,
          hint,
        });
      } else {
        Modal.error({
          title: hint || "安装失败",
          content: "",
        });
      }
    };
    getLibraryToken({
      libraryId: libraryId,
    })
      .then(
        (o) => {
          const { data } = o.data;
          let fileUrl = data;
          if (!fileUrl) {
            Modal.error({
              title: "安装失败，请稍后重试",
              content: "",
            });
          }
          installApp({
            fileUrl,
            projectId: projectId,
            id: libraryId,
            accountId: this.props.accountId,
          })
            .then(
              (res) => {
                const { status = null, data = {} } = res;
                const { exception = "", state = null } = data;
                if (status === 402 && state === 13) {
                  cancelFn(
                    "版本授权已过期，请续费后使用",
                    exception || "授权已过期",
                    data.data
                  );
                  return;
                } else if (exception) {
                  cancelFn("", exception);
                  return;
                }
                if (data) {
                  const { appId = "", errorCode = "" } = data;
                  if (!appId) {
                    //错误码 1=应用数量超标，6=工作表数量超标
                    if (errorCode === 1) {
                      cancelFn("请付费升级以扩展更多用量", "应用数量超标");
                      return;
                    }
                    if (errorCode === 6) {
                      cancelFn("请付费升级以扩展更多用量", "工作表数量超标");
                      return;
                    }
                    this.setState({ showDialog: false });
                  }
                  setTimeout(() => {
                    let url = isMobile
                      ? `/mobile/app/${appId}`
                      : `/app/${appId}`;
                    window.location.assign(url);
                  }, 3000);
                } else {
                  cancelFn();
                }
              },
              (data) => {}
            )
            .catch((error) => {
              console.log(error);
            });
        },
        () => {}
      )
      .catch((error) => {
        console.log(error);
      });
  };

  appDone = () => {
    const {
      projects = [],
      accountId = "",
      isMobile,
      categoryId = "",
      libraryId = "",
    } = this.props;
    let flag = this.IsPC();
    if (flag || isMobile) {
      if (!accountId) {
        const categoryIdStr = !categoryId ? "" : `categoryId=${categoryId}&`;
        let url =
          "/login.htm?ReturnUrl=" +
          encodeURIComponent(
            `${window.location.origin}/app/lib?${categoryIdStr}libraryId=${libraryId}`
          );
        window.location.assign(url);
      } else {
        if (!projects.find((item) => !item.cannotCreateApp)) {
          Modal.info({
            title: "您没有可安装模板的组织",
          });
          return;
        }

        this.setState({ showDialog: true });
        // 存在projectId，直接安装，无需选择网络
        if (this.props.projectId) {
          this.installAppFn(this.props.projectId, libraryId);
        }
      }
    } else {
      let ua = window.navigator.userAgent.toLowerCase();
      // 打开某手机上的某个app应用
      if (ua.match(/MicroMessenger/i) == "micromessenger") {
        Modal.info({
          title: "点击右上角，选择在浏览器中打开",
          content: "",
        });
      } else {
        window.open(`mingdao://installapk/${libraryId}`);
        setTimeout(function () {
          if (/iphone|ipad|ipod/.test(ua)) {
            window.open("http://itunes.apple.com/cn/app/id468630782");
          } else if (/android/.test(ua)) {
            window.open("//filepub.mingdao.com/mingdao.apk?r=" + Math.random());
          } else {
            window.open("/m/mobile.htm");
          }
        }, 1000);
      }
    }
  };

  renderTag = (categoryInfo) => {
    let url = !this.props.accountId ? "/library" : "/app/lib";
    return (
      <React.Fragment>
        {map(categoryInfo, ({ categoryId, name }) => (
          <Link
            key={`more-${categoryId}`}
            to={`${url}?categoryId=${categoryId}`}
          >
            <span className="tag Font13 mRight8">{name}</span>
          </Link>
        ))}
      </React.Fragment>
    );
  };

  render() {
    const { data = [] } = this.state;
    if (data.length <= 0) {
      return (
        <div className="mTop100">
          <Load />
        </div>
      );
    }
    const isMobile = () => {
      var sUserAgent = navigator.userAgent.toLowerCase();
      var bIsIpad = sUserAgent.match(/ipad/i) == "ipad";
      var bIsIphoneOs = sUserAgent.match(/iphone os/i) == "iphone os";
      var bIsMidp = sUserAgent.match(/midp/i) == "midp";
      var bIsUc7 = sUserAgent.match(/rv:1.2.3.4/i) == "rv:1.2.3.4";
      var bIsUc = sUserAgent.match(/ucweb/i) == "ucweb";
      var bIsAndroid = sUserAgent.match(/android/i) == "android";
      var bIsCE = sUserAgent.match(/windows ce/i) == "windows ce";
      var bIsWM = sUserAgent.match(/windows mobile/i) == "windows mobile";

      return (
        bIsIpad ||
        bIsIphoneOs ||
        bIsMidp ||
        bIsUc7 ||
        bIsUc ||
        bIsAndroid ||
        bIsCE ||
        bIsWM
      );
    };
    const {
      name,
      intro,
      description,
      iconUrl,
      iconColor,
      cover,
      pictures,
      mobilePictures,
      video,
      categoryInfo,
    } = this.state.data;
    return (
      <React.Fragment>
        {!this.state.showVideo ? (
          ""
        ) : (
          <VideoCon
            video={video}
            showVideo={this.state.showVideo}
            onCancel={() => this.setState({ showVideo: false })}
          />
        )}
        <div className="itemImg Block Relative">
          <img
            className="Absolute"
            src={`${cover.fileUrl}?imageView2/0/w/1200/h/900/format/jpg/interlace/1/q/90`}
          />
        </div>
        <div className="detailsHead">
          <div className="headLeft">
            <div
              className="appIcon Left mRight20"
              style={{ backgroundColor: iconColor }}
            >
              <SvgIcon
                url={iconUrl}
                fill="#fff"
                size={40}
                addClassName="mTop20"
              />
            </div>
            <div className="appTitle Left">
              <span className="Gray Font34 TxtLeft title Bold WordBreak">
                {name}
              </span>
              <br />
              {this.renderTag(categoryInfo)}
            </div>
          </div>
          <div className="headBtn">
            {!video || !video.viewUrl ? (
              ""
            ) : (
              <div
                className="btnForVideo btn Font16"
                onClick={this.showVideoDialog}
              >
                {/* <i className="icon icon-arrow-right-tip Font16" /> */}
                <img
                  style={{
                    height: "16px",
                    verticalAlign: "text-top",
                  }}
                  className="mRight8"
                  src="data:image/png;base64,"
                />

                {"观看视频"}
              </div>
            )}
            <div
              className={cx("btnForApp Font16 btn", {
                mLeft24: !!video.viewUrl,
              })}
              onClick={this.appDone}
            >
              {"安装模板"}
            </div>
          </div>
        </div>
        <div className="details">
          <div className="Font26 Gray_9e detailsTitle">
            {!intro ? "" : `“${intro}”`}
          </div>
          <p className="Font16 Gray LineHeight28 detailsText">
            <Linkify properties={{ target: "_blank" }}>{description}</Linkify>
          </p>
          <Slideshow
            data={
              !isMobile || !mobilePictures || mobilePictures.length <= 0
                ? pictures
                : mobilePictures
            }
          />
          <div className="appOperation">
            <div className="appTitle TxtMiddle">
              <div
                className="appIcon mRight12"
                style={{ backgroundColor: iconColor }}
              >
                <SvgIcon
                  url={iconUrl}
                  fill="#fff"
                  size={22}
                  addClassName="mTop7"
                />
              </div>
              <span className="Gray Font18 Bold title">{name}</span>
            </div>
            <p className="appDet mTop25 Font20 Gray_75">
              {!intro ? "" : `“${intro}”`}
            </p>
            <div className="btnForApp Font16 btn mTop50" onClick={this.appDone}>
              {"立即安装"}
            </div>
          </div>
        </div>
        {this.state.showDialog ? (
          <AddDialog
            {...this.props}
            visible={this.state.showDialog}
            isMobile={this.props.isMobile}
            // onCancel={() => this.setState({ showDialog: false })}
            libraryId={this.props.libraryId}
            projectId={this.props.projectId}
            installAppFn={this.installAppFn}
          />
        ) : (
          ""
        )}
      </React.Fragment>
    );
  }
}
export default AppDetails;
